import Inputmask from "inputmask";
import 'jquery.inputmask';

var mask = function(){
	return {
		phoneMask: function(){
			$('.phone-mask').inputmask("+380(99)-999-9999");
			$('.phone-mask').attr('placeholder',"+380(__) ___-____")
		},
		initialState: function(){
		},
		init: function(){
			this.phoneMask();
			this.initialState();
		}
	}
}
export default mask



