import mask from '../../js/mask.js'

import interfaceUnit from '../../js/interfaceUnit.js'
// import cabModals from '../../js/cabModals.js'
import modals from '../../js/modals'

import '../../libs/jquery-ui/jquery-ui'


const calendar = function(){
	console.log('calendar');
	$('#calendar').datepicker({
		onSelect: function (date) {
			console.log(date);

			$('.holidays').append(
				`<div class="holiday">
						<span>${date}</span> 
						<span class="delete-date">[X]</span>
				</div>`)
		}
	});
	$('#calendar').datepicker("setDate", "20.08.2018");

	$('.holidays').on('click', '.delete-date', function () {
		console.log('delete-click');
		$(this).closest('.holiday').remove();
	})
};

const itemPicker = function(){
	$('.picked-items-box select').on('change', function(event){
		const containerBox = $(this).parent();
		const itemsContainer = containerBox.find('.picked-items');

		console.log('---select was changed');
		const selectedIndex = this.selectedIndex;

		if (selectedIndex === 0) return

		let text;
		let value;

		$(this).find('option').each(function(index){
			console.log(index);
			if ( index === selectedIndex ) {
				text = $(this).html();
				value = $(this).val();
			}
		});

		let choosedValue = [];

		itemsContainer.find('.picked-items__item').each(function(index){
			let value = $(this).data('value');
			choosedValue.push(value)
		});

		if (choosedValue.includes(value)) return

		console.log(choosedValue);
		console.log(text);
		itemsContainer.append(`
			<div 
				class="picked-items__item"
				data-value="${value}">
				<span>${text}</span>
				<span class="picked-items__delete">[X]</span>
			</div>`);
	});

	$('.picked-items').on('click', '.picked-items__delete', function(){
		const select = this.closest('.picked-items-box').querySelector('select');
		$(this).parent().remove();
		select.selectedIndex = 0;
		console.dir(select);


	})
}

// cabProdCatalog
;(function(){
	$('#cabProdCatalog').find('.cab__prod-cat').each(function(index){
		console.log(index);
		let k = $(this).data("nesting");
		$(this).css('padding-left', 40*k);
	})
})();


var tabSwitch = function(){
	$('.cab-side__tab').on('click', function(){
		$('.cab-side__tab').removeClass('cab-side__tab--active');
		$(this).addClass('cab-side__tab--active');
	})
};

console.log('cab_prov_doc')
$(document).ready(function(){
	mask().init();
	interfaceUnit().init();
	// cabModals().init();
	tabSwitch();
	calendar();
	itemPicker();
	modals().init();
	modals().showModal('comment-modal')
});



